import refs from "./utility/refs";
import drawGallery from "./utility/drawGallery";

export default {
  pageNumber: 0,
  storedQuery: "",
  fetchQuery(searchQuery) {
    if (searchQuery !== this.storedQuery) {
      this.pageNumber = 0;
      refs.gallery.innerHTML = "";
      this.offset = 0;
    }
    this.pageNumber++;
    this.storedQuery = searchQuery;
    this.offset = refs.gallery.offsetHeight;
    fetch(
      `https://pixabay.com/api/?image_type=photo&orientation=horizontal&q=${searchQuery}&page=${this.pageNumber}&per_page=12&key=14619699-a58095564fbdec9b5516f2680`
    )
      .then(res => res.json())
      .then(data => drawGallery(data.hits))
      .then(() => {
        setTimeout(() => window.scrollTo(0, this.offset), 1000);
        console.log(this.offset);
      })
      .catch(console.warn);
  }
};
