import * as basicLightbox from "basiclightbox";

export default function createLightbox({ target }) {
  if (target.nodeName === "IMG") {
    const myLightbox = basicLightbox.create(
      `<div><img src='${target.dataset.lightbox}'></div>`
      //   {
      //     onClose: instance => {
      //       console.log("oh");
      //     }
      //   }
    );
    myLightbox.show();
  }
}
